import { Component, Host, h, Prop, State, EventEmitter, Event } from '@stencil/core';
import { joinClass } from '../../../utils/joinClass';
import i18next from 'i18next';

@Component({
  tag: 'charma-input',
  styleUrl: 'charma-input.scss',
  shadow: true,
})
export class CharmaInput {
  @Prop() label = '';
  @Prop() name = 'charma-input';
  @State() focused = false;
  @Prop({ reflect: true }) value = '';
  @Prop() placeholder = '';
  @Prop() required = false;
  @Prop() error = '';

  @Event() valueChange: EventEmitter<string>;

  setFocus = () => {
    this.focused = !this.focused;
  };

  handleChange = (event: Event) => {
    this.valueChange.emit((event.target as HTMLInputElement).value);
  };

  render() {
    const errorBlock = <span class="text-xs text-red-500">{i18next.t(this.error)}</span>;
    return (
      <Host>
        <div class="grid gap-2 font-ns">
          <label
            class={joinClass('text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70', this.focused ? ' border-black' : '')}
            htmlFor={this.name}
          >
            {this.label} {this.required && <span class="text-red-500">*</span>}
          </label>
          <div class="flex flex-col gap-1">
            <input
              type="text"
              class={joinClass(
                'flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-sm shadow-sm transition-colors file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50',
                this.error ? 'border-red-500' : '',
              )}
              id={this.name}
              name={this.name}
              placeholder={this.placeholder}
              onChange={this.handleChange}
              value={this.value}
            />
            {this.error && errorBlock}
          </div>
        </div>
      </Host>
    );
  }
}

